.calc__container {
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 2em;
}

.calc__sub-heading {
  margin-bottom: 1em;

  text-align: center;
  text-transform: uppercase;

  font-weight: 600;

  color: #b42330;
}

.calc__form {
  table {
    width: 100%;
  }

  input[type='text'] {
    padding: 5px;

    border: 1px solid rgba(0, 0, 0, 0.5);
    background-color: rgba(255, 255, 255, 0.5);
  }

  select {
    padding: 5px;

    border: 1px solid rgba(0, 0, 0, 0.5);
    background-color: rgba(255, 255, 255, 0.5);
  }

  tr {
    display: flex;

    flex-wrap: wrap;
  }

  td {
    width: 100%;
    margin-bottom: 0.5em;
  }
}

.calc__final {
  justify-content: flex-end;

  .c1 {
    text-align: end;
  }

  .c2 {
    width: auto;
  }

  input[type='text'] {
    max-width: 200px;
    margin-left: auto;
    padding: 10px;

    border: 2px dotted #9b242c;
  }
}

.c1 {
  flex-grow: 1;
}

.c2 {
  input {
    width: 100%;
  }

  select {
    width: 100%;
  }
}

.calc__button {
  margin-bottom: 2em;
}

.calc__item {
  margin-bottom: 1em;

  .bold-text {
    width: auto;

    color: #9b242c;
  }
}

.calc__sub-list {
  margin-top: 1em;
  margin-left: 1em;

  li {
    margin-bottom: 1em;
  }
}

@media(min-width: $widthTablet) {
  .calc__container {
    max-width: 45%;
  }

  .calc__containers {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
  }
}
