.black-bg {
  color: $white;
  background: rgba(0, 0, 0, 0.8);

  .arrow-svg {
    fill: $white;
  }
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  flex-direction: column;
}

.white-bg {
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  flex-direction: column;

  .arrow-svg {
    fill: $darkRed;
  }

  .team__container {
    color: $white;
  }

  .contacts__svg {
    fill: $black;
  }
}

