.how__icon {
  position: relative;

  display: flex;

  width: 50px;
  height: 50px;
  margin-right: 10px;

  border: solid 1px black;
  border-radius: 50%;
  background-color: white;

  align-items: center;
  justify-content: center;

  span {
    position: absolute;
    left: -20px;

    display: flex;

    width: 25px;
    height: 25px;

    font-weight: 600;

    color: $white;
    border: solid 1px black;
    border-radius: 50%;
    background-color: $red;

    justify-content: center;
  }
}

.how__svg {
  width: 30px;

  fill: $black;
}

.how__list {
  display: flex;

  margin-left: 10%;

  flex-wrap: wrap;
}

.how__item {
  display: flex;

  width: 100%;
  margin-bottom: 1em;

  align-items: center;
}

.how__description {
  width: 70%;

  line-height: 1.3em;
}

@media(min-width: 600px) {
  .how__item {
    width: 49%;
  }
}

@media(min-width: 1024px) {
  .how__list {
    margin-left: 2%;
  }

  .how__item {
    width: 25%;
  }
}
