.contacts {
  padding-bottom: 3em;
}

.contacts__container {
  display: flex;

  flex-wrap: wrap;
}

.contacts__container--mini {
  .contacts__item {
    font-size: 1em;

    background-color: rgba(0, 0, 0, 0.8);
  }

  .contacts__list {
    width: 49%;
  }

  .contacts__item {
    margin-left: 0;
  }
}

.contacts__list {
  flex-grow: 1;
}

.contacts__map {
  overflow: hidden;

  height: 300px;

  flex-grow: 1;
}

.contacts__item {
  position: relative;

  margin-bottom: 1em;
  margin-left: 54px;
  padding: 7px;

  font-size: 1.5em;

  color: $white;
  background-color: $darkRed;
}

.contacts__span {
  display: block;

  width: 100%;
}

.contacts__tel .contacts__svg {
  top: 0;
}

.contacts__svg {
  position: absolute;
  top: -5px;
  left: -55px;

  fill: $white;
}

.contacts__tel {
  font-size: 1.8em;
  font-weight: 700;

  .contacts__link {
    display: block;

    width: 100%;
  }
}

.contacts__link {
  text-decoration: none;

  color: $white;
}

@media(min-width: $widthTablet) {
  .contacts__map {
    height: 500px;
  }
}

@media(min-width: $widthDesktop) {
  .contacts__container {
    flex-wrap: nowrap;
  }

  .contacts__list {
    width: 49%;
    padding: 1em;
  }

  .contacts__map {
    width: 49%;
  }
}
