*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;

  font-family: 'Montserrat', 'Arial', sans-serif;
}

body {
  margin: 0;

  font-size: 14px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
}

header,
footer {
  width: 100%;
}

main {
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  padding: 20px 0;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1310px;
  margin: 0 auto;
  padding: 0 30px;

  @include media-mobile {
    padding: 10px;
  }
}

body {
  background: url('../img/bg.jpg') 0 0 no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.heading {
  margin-bottom: 0.7em;

  text-align: center;
  text-transform: uppercase;

  font-size: 1.8em;
  font-weight: 300;
  line-height: 1.3em;
}

.sub-heading {
  width: 85%;
  margin-bottom: 0.3em;

  text-align: center;
  text-transform: lowercase;

  font-size: 1.3em;
  font-weight: 500;
  line-height: 1.6em;
}

.swiper-button-prev {
  transform: rotate(180deg);
}

.swiper-slide {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.main-slider.swiper-container {
  width: 100%;
}

.photo__list {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-around;
}

.photo__item {
  margin-right: 0.5em;
  margin-bottom: 1em;
  margin-left: 0.5em;
}

.description {
  margin-bottom: 2em;

  line-height: 1.5em;
}

.lg-backdrop.in {
  opacity: 0.7 !important;
}

.bold-text {
  display: block;

  width: 100%;

  font-weight: 700;
}

.yandex-map,
.yandex-map div {
  width: 100%;
  max-width: 1665px;
  height: 500px;
  margin: 0 auto;
  margin-bottom: 1em;
  padding: 0;
}

@media(min-width: $widthTablet) {
  body {
    font-size: 16px;
  }

  .heading {
    font-size: 36px;
  }
}

.button {
  display: block;
  text-decoration: none;
  text-align: center;

  width: 100%;
  padding: 1em;

  font-weight: 700;

  color: white;
  border: none;
  border-radius: 1.3px;
  background: linear-gradient($red, $darkRed);
}

.swiper-pagination-bullet-active {
  background: $black !important;
}

.swiper-button-next:hover .arrow-svg,
.swiper-button-next:focus .arrow-svg,
.swiper-button-prev:hover .arrow-svg,
.swiper-button-prev:focus .arrow-svg {
  fill: $black;
}

.highlighted-text {
  position: relative;

  padding: 1em;
  padding-left: 30px;

  color: $white;
  background-color: $darkRed;

  &::before {
    position: absolute;
    top: 25px;
    left: 0;

    width: 25px;
    height: 25px;

    content: '';

    background: url('../img/danger.svg') 0 0 no-repeat;
  }
}

@media(min-width: $widthDesktop) {
  body {
    font-size: 18px;
  }

  .heading {
    padding-left: 1em;

    text-align: start;

    font-size: 46px;
  }

  .photo__item {
    margin-right: 8px;
    margin-left: 0;
  }

  .yandex-map {
    width: 49%;
  }

  .yandex-map div {
    width: 100%;
  }
}
