.form-wrapper {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.form__input {
  width: 100%;
  margin-bottom: 1em;
  padding: 10px;

  border: solid 1px $black;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 10px 5px 5px rgb(114, 14, 14);

  &:last-child {
    margin-bottom: 0;
  }

  &::placeholder {
    color: $black;
  }
}

.form__label {
  display: block;

  width: 100%;
  margin-bottom: 2em;
}

@media(min-width: $widthTablet) {
  .form {
    width: 70%;
    max-width: 580px;
  }
}
