.do__heading {
  padding-left: 0;

  text-align: center;
}

.do__list {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-around;
  margin-left: 1em;
}

.do__item {
  position: relative;

  display: flex;

  margin-bottom: 1em;
  padding-top: 1em;

  text-decoration: none;

  line-height: 1.3em;

  color: $black;
  color: $white;
  border: solid 1px $black;
  background-color: rgba(70, 0, 0, 0.5);

  justify-content: center;
  flex-wrap: wrap;
  align-items: stretch;
  flex-grow: 1;

  &:hover,
  &:focus {
    .do__info {
      display: block;
    }
  }
}

.do__info {
  padding: 1em;
}

.do__p {
  margin-bottom: 1em;
}

.do__img {
  width: 300px;
  height: 200px;
  margin-bottom: 1em;

  aspect-ratio: 6/4;
}

.do__sub-heading {
  width: 100%;
  margin: 0;

  text-decoration: underline dotted 1px;
  text-transform: uppercase;

  font-weight: 300;
}

@media(min-width: $widthTablet) {
  .do__item {
    width: 48%;
    margin-right: 1em;
  }
}

@media(min-width: 1200px) {
  .do__list {
    flex-wrap: nowrap;
  }

  .do__sub-heading {
    font-size: 1em;
  }
}
