.main-slider {
  height: 500px;
}

.main-slider__info {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  align-items: center;

  width: 100%;
  height: 100%;

  background: rgba(255, 255, 255, 0.6);

  flex-wrap: wrap;
  justify-content: center;

  .container {
    padding-top: 1em;
    height: fit-content;
  }
}

.main-slider__img {
  width: 100%;
}

.main-slider__heading {
  margin-bottom: 1em;

  font-size: 3em;
}

.main-slider__text {
  max-width: 500px;
  margin-bottom: 2em;

  font-size: 1.5em;
}

.main-slider__button {
  max-width: 300px;

  font-size: 1em;
}
