.team__list {
  display: flex;

  width: 80%;
  margin: 0 auto;
  margin-bottom: 2em;

  flex-wrap: wrap;
  justify-content: space-around;
}

.team__item {
  display: flex;

  margin-bottom: 2em;

  flex-wrap: wrap;
  justify-content: center;
}

.team__container {
  height: 50%;
  padding: 1em;

  background-color: #252525c4;
}

.team__img {
  aspect-ratio: 6/8;
}

.team__info {
  margin-bottom: 1em;
}

.team__mate span {
  display: block;

  width: 100%;

  font-size: 0.7em;
  font-weight: 300;
}

.team__description {
  font-weight: 600;
}

@media(min-width: 500px) {
  .team__item {
    width: 48%;
  }
}

@media(min-width: 1110px) {
  .team__list {
    justify-content: space-between;
    flex-wrap: nowrap;
  }

  .team__item {
    width: auto;

    align-items: stretch;
  }

  .team__img {
    align-items: flex-start;
  }
}
