.footer__links-container {
background: #9b242cdc;
}

.footer__container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 1em 0;
}
.footer__link {
  text-decoration: none;
  color: $white;
  text-transform: uppercase;
}

.footer__logo-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.footer__logo-container .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer__logo {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
}

.footer__tel {
  font-size: 1.3em;
  font-weight: 600;
  text-decoration: none;
  color: $darkRed;
}

.footer__info {
  display: block;
  width: 90%;
  text-align: center;
  font-size: 0.9em;
  margin-bottom: 0.5em;
}

.footer__list {
  padding: 1em 0;
  border-bottom: 1px solid $white;

  &:last-of-type {
    border-bottom: none;
  }
}

.footer__item {
  padding: 0.5em;
  text-align: center;
}

@media(min-width: $widthTablet) {
  .footer {
    display: flex;
  }
  .footer__links-container {
    width: 50%;
  }
  .footer__logo-container {
    width: 50%;
    flex-direction: column;
    justify-content: center;
  }
}

@media(min-width: $widthTablet) {
  .footer__links-container {
    display: flex;
    flex-wrap: wrap;
    width: 70%;
  }
  .footer__logo-container {
    width: 30%;
  }

  .footer__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
}
