.features {
  overflow: hidden;
}

.features__list {
  display: flex;

  padding: 20px 0;

  flex-wrap: wrap;
  justify-content: space-around;
}

.features__item {
  position: relative;

  display: flex;

  margin-bottom: 2em;
  padding: 20px;

  background-color: #9b242c9e;

  clip-path: polygon(0% 100%, 5% 0%, 100% 0%, 95% 100%);
  flex-wrap: wrap;
  justify-content: center;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:nth-of-type(2n) {
    background-color: #0000009e;

    span {
      color: $darkRed;
    }
  }

  &:first-of-type span {
    transform: translate(-25px, 0px);
  }
}

.features__sub-heading {
  margin-bottom: 1em;

  text-align: center;

  span {
    position: absolute;

    transform: translate(-35px, 0px);

    font-size: 2.5em;
    font-weight: 700;

    color: $black;
  }
}

.features__sub-item {
  position: relative;

  margin-bottom: 0.6em;
  padding-left: 20px;

  &::before {
    position: absolute;
    top: 2px;
    left: 0;

    width: 8px;
    height: 8px;

    content: '';

    background-color: rgba(255, 255, 255, 0.6);
  }
}

@media(min-width:$widthTablet) {
  .features__item {
    width: 80%;
  }
}

@media(min-width:$widthDesktop) {
  .features__list {
    flex-wrap: nowrap;
  }

  .features__item {
    width: 32%;
    margin-bottom: 0;
    padding: 20px 30px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  .features__sub-heading {
    // max-width: 250px;
    span {
      transform: translate(-43px, 0);
    }
  }
}
