.header {
  position: relative;
}

.header__container {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-between;
}

.header__logo-container {
  display: flex;

  justify-content: space-between;
  align-items: center;
  flex-grow: 1;

  span {
    display: block;

    font-size: 0.8em;
  }
}

.header__logo {
  display: block;

  margin-right: 1em;
}

.header__list {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-between;
}

.header__item {
  width: 100%;

  text-align: center;

  clip-path: polygon(0% 100%, 5% 0%, 100% 0%, 95% 100%);
}

.header__link {
  text-decoration: none;

  font-weight: 300;

  color: $white;
}

.header__svg {
  fill: $black;
}

.header-right {
  display: flex;

  flex-wrap: wrap;
  align-items: center;
  flex-grow: 1;
  justify-content: space-around;
}

.header-right__item {
  padding: 0.5em;
}

.header__tel {
  display: flex;

  text-decoration: none;

  font-size: 1.3em;
  font-weight: 600;

  color: #64181c;

  align-items: center;
  justify-content: center;

  svg {
    margin-right: 10px;
  }
}

.header__calc {
  display: flex;

  text-decoration: underline dotted 1px #64181c;

  color: #64181c;

  align-items: center;
  justify-content: center;

  svg {
    margin-right: 10px;
  }
}

.header__close {
  border: none;
  background: transparent;
}

.header__logo-container {
  max-width: 300px;
  margin: 0 auto;
}

@media(max-width: $widthTablet) {
  .header__item {
    padding: 1em 0;

    text-transform: uppercase;

    font-weight: 300;

    background-color: rgba(0, 0, 0, 0.8);

    &:nth-of-type(2n) {
      background-color: #9b242c9e;

      .header__link {
        color: $white;
      }
    }
  }

  .header__description {
    display: none;
  }

  .header__menu-button {
    display: flex;

    width: 40px;
    height: 40px;

    border: none;
    background: transparent;

    justify-content: center;
    align-items: center;
  }

  .header__link {
    font-weight: 600;
  }

  .header__lower.black-bg {
    background-color: rgba(255, 255, 255, 0.8);
  }

  .header__description {
    margin-bottom: 0.5em;

    text-transform: uppercase;

    font-size: 0.9em;
    font-weight: 600;

    color: #64181c;

    span {
      display: block;

      width: fit-content;
      margin-bottom: 0.5em;
    }
  }

  .header__burger--closed,
  .header__close--closed {
    display: none;
  }

  .nav {
    z-index: 11;

    transition: all 1s;
  }

  .nav--hidden {
    position: absolute;
    left: -200%;
  }

  .nav--show {
    position: absolute;
    left: 0;

    display: block;

    width: 100%;
  }
}

@media(min-width: $widthTablet) {
  .header__upper {
    padding: 20px 0;
  }

  .header__list {
    padding: 20px 0;

    flex-wrap: nowrap;
  }

  .header__item {
    clip-path: none;
  }

  .header__link {
    position: relative;
    &::before{
      position: absolute;
      content: '';
      right: 0;
      bottom: -4px;
      left: 0;
      width: 0;
      height: 2px;
      margin: auto;
      transition: all .5s ease-out;
      background: $red;
    }

    &:hover:before,
    &:focus:before{
      width: 100%;
    }
  }

  .header-right {
    margin-left: 1em;

    justify-content: space-between;
  }

  .header-right__item {
    padding: 0 0.5em;
  }

  .header__close,
  .header-menu__button,
  .header__burger {
    display: none;
  }

  .header__logo-container {
    max-width: 350px;
    margin-bottom: 1em;
  }
}

@media(min-width: $widthDesktop) {
  .header__logo-container {
    margin-bottom: 0;
    padding-right: 1em;

    border-right: 1px solid grey;
  }

  .header__container {
    flex-wrap: nowrap;
  }
}
