.works__types {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 2em;
}

.works__type {
  background-color: rgba(0,0,0,0.8);
  color: $white;
  padding: 1em;
  cursor: pointer;
  width: 100%;
  text-align: center;
  clip-path: polygon(0% 100%,5% 0%,100% 0%,95% 100%);
  margin-bottom: 0.3em;

  &:nth-of-type(2n) {
    background-color: #9b242c9e;
  }
}

.works__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.works__button {
  margin-bottom: 1em;
  position: absolute;
  bottom: -10px;
  opacity: 0;
  transform:scale(0);
  background: rgba(255,255,255,0.8);
  clip-path: polygon(0% 100%,5% 0%,100% 0%,95% 100%);
  padding: 1em;
  border: none;
  font-size: 0.9em;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 1s;

  &:hover,
  &:focus {
    background: $white;
  }
}

.works__item {
  position: relative;
  overflow: hidden;
  margin: 0 0.3em;
  margin-bottom: 1em;
}

.works__name {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: center;
  background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.6));
  color: $white;
  text-transform: uppercase;
  opacity: 1;
  transition: all 0.4s ease-in;

  h3 {
    font-weight: 300;
    font-size: 0.9em;
    margin-bottom: 1em;
    margin-left: 1em;
    transition: transform 0.4s ease-in;
    transform: translate(0,0);
  }
}

.works__img {
  transform: scale(1);
  transition: transform 0.4s ease-in;
  max-width: 300px;
}

.works__link {
  position: relative;
  display: flex;
  flex-wrap: wrap;

  &:hover .works__button,
  &:focus .works__button {
    transform: scale(1);
    opacity: 1;
    bottom: 0;
  }

  &:hover .works__name,
  &:focus .works__name {
    background: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.9));
  }

  &:hover h3,
  &:focus h3 {
    transform: translate(0,-60px);
  }

  &:hover .works__img,
  &:focus .works__img {
    transform: scale(1.2);
  }
}
@media(min-width: 400px) {
  .works__type {
    width: 50%;
    background-color: #9b242c9e;
  }
}

@media(min-width:$widthTablet) {
  .works__types {
    justify-content: end;
    font-size: 0.9em;
  }
  .works__type {
    width: auto;
  }

  .works__list {
    justify-content: flex-start;
  }
}
