.clients {
  padding-bottom: 0;
}

.clients__container {
  background-color: $white;
}

.clients__list {
  display: flex;

  padding: 3em 0;

  flex-wrap: wrap;
  justify-content: space-around;
}

.clients__item {
  display: flex;

  width: 34%;

  align-items: center;

  &--square {
    width: 110px;
  }
}

@media(min-width: $widthTablet) {
  .clients__item {
    width: 26%;
  }
}

@media(min-width: $widthDesktop) {
  .clients__item {
    width: 16%;
  }
}
