.about__p {
  margin-bottom: 1em;
}

.about__photos {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-around;
}

.about__photo {
  margin-bottom: 1em;

  clip-path: polygon(0% 100%, 10% 0%, 100% 0%, 90% 100%);
}

@media(min-width: $widthTablet) {
  .about__photo {
    width: 50%;

    &:nth-of-type(2n) {
      position: relative;
      right: 35px;
    }
  }
}
